.c-voice-box {
  $this: #{&};

  margin: auto;
  padding: 100px 20px 0;
  position: relative;
  z-index: 10;
  display: none;

  @include mq-min-max-tb {
    padding: 50px 20px 0;
  }

  @include mq-max-sp {
    padding: 100px 30px 0;
  }

  &__inner {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    max-width: 1300px;
    margin: auto;
  }

  &__fix-box {
    width: 38.88%;
    padding-right: 30px;

    @include mq-min-max-tb {
      padding-left: 20px;
    }

    @include mq-max-sp {
      width: 100%;
      padding-right: 0;
    }

    &-heading {
      font-size: 4rem;
      line-height: 1.3;
      font-weight: 400;
      color: $baseColor;
      position: relative;
      display: flex;

      @include mq-min-max-tb {
        font-size: 3.2rem;
      }

      @include mq-max-sp {
        font-size: 3.2rem;
        flex-direction: column;
        text-align: center;
      }

      &-icon {
        width: 52px;
        height: 48px;
        background: url(/assets/img/recipes/ico_product_yellow.svg) no-repeat center center;
        background-size: cover;
        position: relative;
        margin-right: 15px;
        display: inline-flex;
        align-items: center;
        justify-content: center;

        @include mq-min-max-tb {
          width: get-vw(52px);
          height: get-vw(48px);
          background-size: get-vw(52px) get-vw(48px);
          top: 10px;
        }

        @include mq-max-sp {
          margin: auto;
        }

        > .c-comment-icon {
          width: 25px;
          height: 25px;
          background: url(/assets/img/svg/comment-dots-regular.svg) no-repeat center center;
          background-size: cover;

          @include mq-min-max-tb {
            width: 20px;
            height: 20px;
            background-size: cover;
          }
        }
      }
    }

    &-text {
      font-size: 1.6rem;
      margin-top: 20px;
      color: #777;

      @include mq-max-sp {
        font-size: 1.4rem;
        text-align: center;
      }
    }
  }

  &__slider-box {
    width: 58.73%;
    box-shadow: 20px 20px 35px 0px rgba(0,0,0,0.1);
    position: relative;

    @include mq-max-sp {
      width: 100%;
      margin: 50px 0 150px;
      border-radius: 20px;
      text-align: center;
    }

    .swiper-box-control-trigger {
      position: absolute;
      top: auto;
      bottom: -80px;
      left: 50%;
      transform: translateX(-50%);

      .swiper-button-prev {
        left: 0;
      }
      .swiper-button-right {
        left: 40px;
      }

      .swiper-button-prev,
      .swiper-button-next {
        left: auto;
        right: auto;
        position: relative;
        outline: none;
        width: 34px;
        height: 34px;
        background: #fff;
        border-radius: 100%;
        box-shadow: 0px 3px 20px 0px rgba(0,0,0,0.16);
        display: inline-flex;
        align-items: center;
        justify-content: center;
        transition: all .3s;

        .fas {
          color: #DCDCDC;
          transition: all .3s;
        }

        &:hover {
          transform: scale(0.8);
          background: $baseColor;

          .fas {
            transform: scale(0.9);
            color: $yellow;
          }
        }
      }
    }
  }
}

.swiper-slide {
  text-align: left;

  .c-card-voice {
    border-radius: 20px;
    background: $white;
    padding: 55px;
    text-align: left;

    @include mq-max-sp {
      padding: 40px 20px;
      text-align: center;
    }

    &__heading {
      font-size: 3.2rem;
      color: $yellow;
      line-height: 1.3;

      @include mq-max-sp {
        font-size: 2.2rem;
      }
    }

    &__text {
      margin-top: 20px;
      font-size: 1.8rem;
      color: $gray;

      @include mq-max-sp {
        font-size: 1.4rem;
      }
    }

    &__author {
      $author: #{&};

      display: flex;
      flex-wrap: wrap;
      margin-top: 20px;

      @include mq-max-sp {
        flex-direction: column;
      }

      &-icon {
        width: 52px;
        height: 48px;
        background: url(/assets/img/recipes/ico_man_yellow.svg) no-repeat center center;
        background-size: cover;

        @include mq-min-max-tb {
          width: get-vw(52px);
          height: get-vw(48px);
        }

        @include mq-max-sp {
          margin: auto;
        }
      }

      &-staff {
        flex: 1;
        padding-left: 20px;

        @include mq-max-sp {
          padding-left: 10px;
        }
      }

      &-staff-name {
        font-size: 2.2rem;
        color: $baseColor;

        @include mq-max-sp {
          font-size: 1.8rem;
        }
      }

      &-staff-position {
        font-size: 1.4rem;
        font-weight: 300;
        color: $gray;

        @include mq-max-sp {
          font-size: 1.3rem;
        }
      }
    }
  }
}
